/*-------------------------------------------*/
/* BASIC SETTINGS */
/*-------------------------------------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  background-color: #FFF;
  text-rendering: optimizeLegibility;
  width: 1600px;
